import React from "react";
import PropTypes from "prop-types";
import {Link} from "react-router-dom";
import "./Movie.css";

function Movie({id, year, title, summary, poster, genres, trailer}){
    const trailerLink = `https://www.youtube.com/watch?v=${trailer}`;
    return (
            <div className="movie">
                <Link to={{
                    pathname: `/movie/${id}`,
                    state: {
                        id,
                        year,
                        title,
                        summary,
                        poster,
                        genres
                    }
                }}>
                    <img src={poster} alt={title} title={title}></img>
                    <div className="movie__data">
                        <h3 className="movie__title">{title}</h3>
                        <h5 className="movie__year">{year}</h5>
                        <ul className="movie__genres">{
                            genres.map((genre, index) => <li key={index} className="genres__genre">{genre}</li>)
                        }</ul>
                        <p className ="movie__summary">{summary.slice(0,180)}...</p>
                        <button className="movie__btn" onClick={() => window.open(trailerLink, "_blank")}>See more</button>
                    </div>
                </Link>
            </div>
    );
}

Movie.prototype = {
    id: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    summary: PropTypes.string.isRequired,
    poster: PropTypes.string.isRequired,
    year: PropTypes.number.isRequired,
    genres: PropTypes.arrayOf(PropTypes.string).isRequired,
    trailer: PropTypes.string
}

export default Movie;